export default {
  namespaced: true,

  state: {
    rs: [],
    rowActive: {},
    sources: [
      '1500784126879139',
      'masakanboenda',
      'arkme2',
      '1451662071520888',
      'masakyuk',
      '229503917487950',
      '123128641610077',
    ],
  },

  mutations: {

    SET_DATA_SOURCE(state, payload) {
      state.rs = payload;
    },

    ADD_DATA_SOURCE(state, payload) {
      state.rs = [
        ...payload,
        ...state.rs,
      ];
    },

    SET_ROW_ACTIVE(state, payload) {
      state.rowActive = payload;
    },
  },

  actions: {

    fetchData({ state, commit }) {
      return new Promise((resolve, reject) => {

        // FB.api('/1500784126879139/feed?fields=message,picture', response => {
        //   commit('SET_DATA_SOURCE', response.data);
        //   resolve(response);
        // });

        state.sources.forEach(source => {
          FB.api('/' + source + '/feed?fields=message,picture,likes.limit(1).summary(true)', response => {
            commit('ADD_DATA_SOURCE', response.data);
          });
        });

        resolve();
      });
    },

    moveRowById({ getters, commit }, payload) {
      let rowActive = {};

      getters.rs.forEach(row => {
        if (row.id == payload)
          rowActive = row;
      });

      commit('SET_ROW_ACTIVE', rowActive);
    },

  },

  getters: {

    rs(state) {
      let i = 0;

      let title = '';
      let excerpt = '';
      let message = '';

      let xMessage = [];
      let xTitle = [];

      let rs = state.rs.map(row => {

        title = '';
        excerpt = '';
        message = '';

        if (row.message) {

          message = row.message;
          message = message.replace(/\n/g, "<br>");
          xMessage = message.split(' ');

          for (i = 1; i <= 5; i++) {
            title +=  xMessage[i - 1] + ' ';
          }

          xTitle = title.split('<br>');
          title = xTitle[0];

          for (i = 6; i <= 15; i++) {
            excerpt +=  xMessage[i - 1] + ' ';
          }
        }

        row.title = title;
        row.excerpt = excerpt;
        row.message = message;

        return row;
      });

      return rs.filter(row => {
        return row.picture;
      });
    },

    rowActive(state) {
      return state.rowActive;
    },

  },

}



