<template>
  <b-container fluid>
    <h1>{{ rowRecipe.title }}</h1>
    <hr>

    <b-row>
      <b-col>
        <img :src="rowRecipe.picture"><br>
        <p v-html="rowRecipe.message"></p>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'ListView',

  /************************
  <id="data">
  ************************/
  data () {
    return {
      loading: false,
      state: '',
      title: '',
    };
  },

  /************************
  <id="computed">
  ************************/
  computed: {

    /************************
    <id="mapGetters">
    ************************/
    ...mapGetters('Recipe', {
      rowRecipe: 'rowActive',
    }),

  },

  /************************
  <id="mounted">
  ************************/
  mounted () {
    this.reload();
  },

  /************************
  <id="watch">
  ************************/
  watch: {

    /************************
    <id="$route">
    ************************/
    $route () {
      this.reload();
    },
  },

  /************************
  <id="methods">
  ************************/
  methods: {

    /************************
    <id="mapActions">
    ************************/
    ...mapActions('Recipe', {
      moveRecipeById: 'moveRowById',
    }),

    /************************
    <id="reload">
    ************************/
    reload () {
      let id = this.$route.params.id;
      this.moveRecipeById(id);
    },

  },

}
</script>

<style lang="scss" scoped>

  h1 {
    text-transform: uppercase;
  }

</style>



