<template>
  <section v-if="display">
    <figure>
      <i class="fa fa-refresh fa-spin"></i>
      <slot></slot>
    </figure>
    <!-- <div class="overlay"></div> -->
  </section>
</template>

<script>
export default {

  /************************
  <id="props">
  ************************/
  props: {
    display: {
      type: Boolean,
      default: false,
    },
  },

  /************************
  <id="computed">
  ************************/
  computed: {

    /************************
    <id="rs_">
    ************************/
    rs_ () {
      return this.rs.map(row => {

        if (row.message.length > 30)
          row.excerpt = row.message.substr(0, 30);
        else
          row.excerpt = row.message;

        return row;
      });
    },

  },

  /************************
  <id="mounted">
  ************************/
  mounted () {

  }
}
</script>

<style lang="scss" scoped>
  section {
    z-index: 1000;
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: white;

    figure {
      position: absolute;
      left: 0;
      top: 50%;
      margin-top: -10px;
      width: 100%;
      text-align: center;
      font-size: 20px;
      text-transform: uppercase;
      font-weight: bolder;
      color: black;
    }
  }
</style>



