<template>
  <b-container fluid>
    <h2>{{ title }}</h2>
    <hr>

    <b-row>
      <b-col v-for="row in rsRecipe_">
        <figure>
          <img :src="row.picture"><br>
          <b>
            {{ row.title }}
            <br>
            <div style="color: gray">
              <i class="fa fa-thumbs-up"></i> {{ row.likes.summary.total_count }}
            </div>

            <a :href="'#/recipe/' + row.id">[ Lihat... ]</a>
          </b>
        </figure>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'ListView',

  /************************
  <id="data">
  ************************/
  data () {
    return {
      loading: false,
      state: '',
      title: '',
    };
  },

  /************************
  <id="computed">
  ************************/
  computed: {

    /************************
    <id="mapGetters">
    ************************/
    ...mapGetters('Recipe', {
      rsRecipe: 'rs',
    }),

    /************************
    <id="rsRecipe_">
    ************************/
    rsRecipe_ () {
      let rsRecipe = [];
      let type = '';
      let category = '';

      if (this.$route.params.type) {
        type = this.$route.params.type;

        if (type == 'terpopuler') {
          return this.rsRecipe.slice().sort((a, b) => {
            return b.likes.summary.total_count - a.likes.summary.total_count;
          });
        }

      } else if (this.$route.params.category) {
        category = this.$route.params.category;

        rsRecipe = this.rsRecipe.filter(row => {
          // return row.message.toLowerCase().indexOf(category.toLowerCase()) >= 0;
          return RegExp('\\b'+ category.toLowerCase() +'\\b').test(row.message.toLowerCase());
        });
      }

      return rsRecipe;
    },
  },

  /************************
  <id="mounted">
  ************************/
  mounted () {
    this.reload();
  },

  /************************
  <id="watch">
  ************************/
  watch: {

    /************************
    <id="$route">
    ************************/
    $route () {
      this.reload();
    },
  },

  /************************
  <id="methods">
  ************************/
  methods: {

    /************************
    <id="reload">
    ************************/
    reload () {
      if (this.$route.params.type) {
        this.title = this.$route.params.type;
      } else if (this.$route.params.category) {
        this.title = this.$route.params.category;
      }
    },

  },

}
</script>

<style lang="scss" scoped>

  h2 {
    text-transform: uppercase;
  }

  figure {
    min-height: 210px;
    text-align: center;
  }
</style>



