<template>
  <b-container fluid>
    <b-row>
      <b-col v-for="row in rs_">
        <img :src="row.picture"><br>
        {{ row.excerpt }}
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import FBLoader from '../tools/FBLoader';

export default {
  name: 'HomeView',

  /************************
  <id="data">
  ************************/
  data () {
    return {
      rs: [],
      loading: false,
      state: '',
    };
  },

  /************************
  <id="computed">
  ************************/
  computed: {

    /************************
    <id="rs_">
    ************************/
    rs_ () {
      return this.rs.map(row => {

        if (row.message.length > 30)
          row.excerpt = row.message.substr(0, 30);
        else
          row.excerpt = row.message;

        return row;
      });
    },

  },

  /************************
  <id="mounted">
  ************************/
  mounted () {
    window.location = '#/terpopuler';

    // this.state = 'Loading Facebook API';
    // // this.loading = true;
    //
    // FBLoader.initialize().then(response => {
    //
    // });

  }
}
</script>

<style lang="scss" scoped>
</style>



