<template>
  <section id="app">
    <ui-loading :display="loading">{{ state }}</ui-loading>

    <b-container>
      <b-row>
        <b-col>
          <br>
          <h2>
            <!-- <i class="fa fa-dinner"></i> -->
            Aplikasi<br>
            <sup>Buku Resep Masakan</sup>
          </h2>
          <hr>
        </b-col>
      </b-row>

      <b-row>
        <b-nav vertical class="w-25">
          <b-nav-item v-for="row in rsMenu" :href="row.url">
            <i class="fa" :class="'fa-' + row.icon"></i>
            {{ row.title }}
          </b-nav-item>
        </b-nav>

        <div class="w-75">
          <router-view/>
        </div>
      </b-row>

      <hr>
      <b>By Fadlun Anaturdasa Wibawa @ 2017</b>
      <br>&nbsp;
    </b-container>

  </section>
</template>

<script>
import FBLoader from './tools/FBLoader';
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'App',

  /************************
  <id="data">
  ************************/
  data () {
    return {
      loading: false,
      state: '',
    };
  },

  /************************
  <id="computed">
  ************************/
  computed: {

    /************************
    <id="mapGetters">
    ************************/
    ...mapGetters('Menu', {
      rsMenu: 'rs',
    }),
    ...mapGetters('Recipe', {
      rsRecipe: 'rs',
    }),
  },

  /************************
  <id="mounted">
  ************************/
  mounted () {

    this.state = 'Loading Facebook API ...';
    this.loading = true;

    FBLoader.initialize().then(response => {

      this.state = 'Download all Recipe';
      this.fetchRecipe().then(response => {
        this.loading = false;
      });
    });

  },

  /************************
  <id="methods">
  ************************/
  methods: {

    /************************
    <id="mapActions">
    ************************/
    ...mapActions('Recipe', {
      fetchRecipe: 'fetchData',
    }),
  },
}
</script>

<style lang="scss">
@import url('https://fonts.googleapis.com/css?family=Signika');

#app {
  font-family: Signika, Helvetica, Arial, sans-serif;

  h2 {
    font-weight: bolder;
    sup {
      color: gray;
      text-transform: uppercase;
    }
  }

  h4 {
    color: gray;
  }
}
</style>



